___CSS_LOADER_EXPORT___.push([module.id, `*{
    box-sizing: border-box;
    font-family: "Poppins", sans-serif;
}

body{
    margin: 0;
    padding: 0;
    background-color: #232023;
    color: #d5d5d5;
    display: flex;
    flex-direction: column;
    height: 100vh;
}

#logoImg{
    width: 110px;
    border-radius: 100px;
    margin-left: 2vw;
}

#header{
    display: flex;
    align-items: center;
    font-size: clamp(28px, 5vw, 38px);
    font-weight: 700;
    background-color: #5a5a5a;
    gap: 10px;
}

#main{
    display: flex;
    flex-grow: 1;
    background-color: #232023;
}


/* SIDEBAR STYLES */
#sidebar{
    display: flex;
    flex-direction: column;
    background-color: #2f2d2f;
    padding: 30px;
    width: clamp(220px, 30vw, 300px);
}

.buttonDays, #addProjectBtn{
    display: flex;
    align-items: center;
    border: none;
    background: none;
    color: #dddddd;
    padding: 10px 20px;
    font-size: clamp(14px, 2vw, 16px);
    font-weight: 700;
    cursor: pointer;
    border-radius: 10px;
    margin-bottom: 10px;
    gap: 10px;
}
.buttonDays:hover{
    transform: scale(1.05);
    background-color: #444444;
}
.buttonDays:active{
    transform: scale(0.99);
}

.with-icon::before {
    content: '';
    display: inline-block;
    width: 30px;
    height: 30px;
    background-repeat: no-repeat;
    background-size: contain;
}
#inbox::before {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}
#today::before {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}
#thisWeek::before {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
}

#addProjectBtn{
    background-color: #232023;
    margin: 0 10px;
    font-size: clamp(12px, 2vw, 14px);
}
#addProjectBtn:hover{
    background-color: #545454;
}
#addProjectBtn::before{
    width: 15px;
    height: 15px;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
}
/* SIDEBAR STYLES */


/* CONTENT STYLES */
#content{
    padding: 50px clamp(50px, 12vw, 200px);
}

.headerDay{
    font-size: 32px;
    font-weight: 600;
}
/* CONTENT STYLES */


/* FOOTER STYLES */
#footer{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5px;
    font-weight: 600;
    background-color: #5a5a5a;
    font-size: clamp(14px, 1vw, 18px);
    padding: 10px;
}
#footer img{
    width: 25px;
    height: 25px;
    cursor: pointer;
    border-radius: 40px;
}
#footer img:hover{
    box-shadow: 0 0 5px rgb(9, 183, 0);
    transform: scale(1.1);
}
/* FOOTER STYLES */

